.Component__Wrap{
    display: flex;
    justify-content: center;
	width: 100%;
}
.test{
    width:100%;
    height:100%;
}

/* 위 */
.to-top{
	color:white;
	position: sticky;
	bottom:40%;
    left:100%;
    transform: translate( -50%, -50% );
    text-decoration: none;
    height: 60px;
}
.to-top span {
    position: absolute;
    width: 46px;
    height: 46px;
    border: 1px solid #fff;
    border-radius: 100%;
    box-sizing: border-box;
    right:0;
    margin-right:1rem;
}
.to-top span::after {
    position: absolute;
    top: 50%;
    left: 50%;
    content: '';
    width: 16px;
    height: 16px;
    margin: 0 0 -10px -8px;
    border-left: 1px solid #fff;
    border-bottom: 1px solid #fff;
    -webkit-transform: rotate(-45deg);
    transform: rotate(135deg);
    box-sizing: border-box;
}

/* 아래 */
.to-bottom{
	color:white;
    position: sticky;
	bottom:65%;
    left:100%;
    transform: translate( -50%, -50% );
    text-decoration: none;
}
.to-bottom span {
    position: absolute;
    width: 46px;
    height: 46px;
    border: 1px solid #fff;
    border-radius: 100%;
    box-sizing: border-box;
    right:0;
    margin-right:1rem;
}
.to-bottom span::after {
    position: absolute;
    top: 50%;
    left: 50%;
    content: '';
    width: 16px;
    height: 16px;
    margin: -12px 0 0 -8px;
    border-left: 1px solid #fff;
    border-bottom: 1px solid #fff;
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
    box-sizing: border-box;
}